import React from 'react';
import ReactDOM from 'react-dom';

import * as serviceWorker from './serviceWorker';
import App from './App';

//[Incio] Configuraes iniciais do Redux
import {Provider} from 'react-redux'
import {createStore, applyMiddleware} from 'redux'
import thunk from 'redux-thunk'
import multi from 'redux-multi'
import mainReducer from './store'

//devTools  a configurao para uma extenso no Google Chrome (depurao de cdigo no redux)
const devTools = window.__REDUX_DEVTOOLS_EXTENSION__ 
      && window.__REDUX_DEVTOOLS_EXTENSION__()
const store = applyMiddleware(thunk, multi)(createStore)(mainReducer, devTools)


ReactDOM.render(
    <Provider store={store}>
        <App />
    </Provider>
    , document.getElementById('root'));

//[Trmino] Configuraes iniciais do Redux


serviceWorker.unregister();
